import React, {useContext, useEffect, useState} from 'react';
import {store} from "../../store/store";
import {
  Divider,
  Typography,
  Box, Paper,
  Avatar,
  Menu,
  MenuItem,
  IconButton,
  Grid
} from '@mui/material';
import {styled} from '@mui/material/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import ThumbUpAltRoundedIcon from '@mui/icons-material/ThumbUpAltRounded';
import AddCommentRoundedIcon from '@mui/icons-material/AddCommentRounded';
import FeedDetail from './FeedDetail';
import MoreMenu from './MoreMenu';
import axios from "axios";

//   5 ,  ... 
const Content = styled(Typography)`
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
`

export default function Feed(props) {
  const [state, dispatch] = useContext(store);
  const {commentCount, id, isLiked, likeCount, writer, content, createTime} = props.feed;
  const [feedDetail, setFeedDetail] = useState(null);

  const comments = [
    {
      "comment_idx": 1,
      "name": "1",
      "image": "https://placeimg.com/100/100/people/1",
      "content": "      .      . ,    .    .      .      . ,    .    .      .      . ,    .    ."
    },
    {
      "comment_idx": 2,
      "name": "2",
      "image": "https://placeimg.com/100/100/people/2",
      "content": "    ,    , .      .    .  ,      .         ."
    }
  ];
  const [open, setOpen] = useState(false);
  const [anchor, setAnchor] = useState(null);

  const openContent = async () => {
    await axios.get(`/feed/${id}`, {
      headers: {authorization: localStorage.getItem('token')}
    })
      .then(res => {
        setFeedDetail(res.data);
        setOpen(true);
      })
      .catch(error => console.error(error))
  };
  const closeContent = () => {setOpen(false)};
  const handleClickProfile = (e) => {setAnchor(e.currentTarget)};
  const handleCloseProfile = () => {setAnchor(null)};
  const handleClickProfileView = () => {
    props.getUser([writer.name, props.image]);
    dispatch({type: 'ChangeMode', payload: 'PROFILE'})
  };

  useEffect(() => {
    console.log(props.feed)
  },[])

  return (
    <>
      <Paper sx={{margin: '14px'}}>
        <Grid container>
          <Grid item xs={11} sx={{cursor: 'pointer'}} onClick={openContent}> {/*  */}
            <Box p={4}>
              <Content>
                {content.split('\n').map((value, index) => (
                  <Typography key={index}>{value === '\r' ? <br/> : value}</Typography>
                ))}
              </Content>
            </Box>
          </Grid>

          <Grid item xs={1} p={2}> {/*   */}
            <MoreMenu
              feedId={id}
              writer={writer.id}
              feedList={props.feedList}
              getFeedList={props.getFeedList}/>
          </Grid>
        </Grid>

        <Divider/>

        <Grid container p={2}>
          <Grid item xs={10}> {/* ,  */}
            <Box>
              <IconButton>
                <ThumbUpAltRoundedIcon color={isLiked ? 'primary' : 'action'} sx={{fontSize: 30}}/>
              </IconButton>{likeCount}
              <IconButton>
                <AddCommentRoundedIcon sx={{fontSize: 30}}/>
              </IconButton>{commentCount}
            </Box>
          </Grid>

          <Grid item xs={2}> {/*  */}
            <Box>
              <Grid container spacing={1} sx={{cursor: 'pointer'}} onClick={handleClickProfile}>
                <Grid item>
                  <Avatar alt="profile image" src={props.image}/>
                </Grid>
                <Grid item>
                  <Typography>{writer.name}</Typography>
                  <Typography variant="body2" color="textSecondary">{createTime.substring(0, 10)}</Typography>
                </Grid>
              </Grid>

              <Menu anchorEl={anchor} open={Boolean(anchor)} onClose={handleCloseProfile}
                    anchorOrigin={{
                      vertical: 'bottom',
                      horizontal: 'center',
                    }}
              >
                <MenuItem onClick={handleClickProfileView}></MenuItem>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
              </Menu>
            </Box>
          </Grid>

        </Grid>
      </Paper>

      {/*    */}
      <Dialog open={open} onClose={closeContent} fullWidth maxWidth='md'>
        <DialogContent>
          <FeedDetail
            feedDetail={feedDetail}
            comments={comments}
            feedList={props.feedList}
            getFeedList={props.getFeedList}
            closeContent={closeContent}
          />
        </DialogContent>
      </Dialog>
    </>
  );
}